import {NumberItem} from '../models/NumberItem'
import * as StringConstans from '../constants/StringConstants'

export default class ParseItem {
  constructor() {
    this.NumberArr = [];
    this.parseNumber();
  }

  parseNumber() {
    let arr = [];
    let parseData = JSON.parse(StringConstans.BaseData);
    parseData.forEach(x => {
      let item = new NumberItem();
      arr.push(Object.assign(item, x));
    });
    this.NumberArr = arr;
  }

}



