import * as ActionTypes from '../../constants/ActionTypes';
import bus from '../../business/GlobalApplication'

export default {
  state: {
    numbers: []
  },
  getters: {
    [ActionTypes.GetNumbers]: state => state.numbers
  },
  mutations: {
    [ActionTypes.GetNumbers](state, item) {
      state.numbers = item;
    }
  },
  actions: {
    [ActionTypes.GetNumbers]({commit, state}) {
      commit(ActionTypes.GetNumbers, bus.parseItem.NumberArr);
    }
  }
}



