
import {mapActions, mapGetters} from 'vuex'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import InlineXSwitch from 'vux/src/components/inline-x-switch/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import * as ActionTypes from '../constants/ActionTypes';

export default {
name: "PickNumber",
components: {
Checker, CheckerItem, Group, Divider, XSwitch, InlineXSwitch, CheckIcon, XButton, Flexbox, FlexboxItem
},
data() {
return {
animalValue: [-1],
numsValue: [-1],
colorValue: [-1],
singleValue: [-1],
animalSwitch: false,
numberSwitch: false,
colorSwitch: false,
singleSwitch: false,
selectAll: false,
lock: false
}
},
mounted: function () {
this.getNumbers();
},
computed: {
...mapGetters({
Numbers: ActionTypes.GetNumbers
}),
selectValue() {
return this.Numbers.filter(x => x.Selected === true).sort(function (x, y) {
return x.Value - y.Value;
})
}
},
methods: {
...mapActions({
getNumbers: ActionTypes.GetNumbers
}),
selectAnimal(value) {
if (this.lock)
return;
if (this.animalSwitch) {
this.Numbers.forEach(x => {
if (x.Animal.Type === value)
x.Selected = false;
});
} else {
let contains = this.animalValue.indexOf(value) > -1;
if (contains) {
let selectCount = this.Numbers.filter(x => x.Animal.Type === value && x.Selected === false).length;
if (this.getShowCount() + selectCount >= 30) {
this.$vux.toast.show({
text: 'So more count',
type: 'warn'
});
this.animalValue.pop();
return;
}
}
this.Numbers.forEach(x => {
if (x.Animal.Type === value)
x.Selected = contains;
});
}
},
selectTailNumber(value) {
if (this.lock)
return;
if (this.numberSwitch) {
this.Numbers.forEach(x => {
if (x.TailValue === value)
x.Selected = false;
});
} else {
let contains = this.numsValue.indexOf(value) > -1;
if (contains) {
let selectCount = this.Numbers.filter(x => x.TailValue === value && x.Selected === false).length;
if (this.getShowCount() + selectCount >= 30) {
this.$vux.toast.show({
text: 'So more count',
type: 'warn'
});
this.numsValue.pop();
return;
}
}
this.Numbers.forEach(x => {
if (x.TailValue === value)
x.Selected = contains;
});
}
},
selectColor(value) {
if (this.lock)
return;
if (this.colorSwitch) {
this.Numbers.forEach(x => {
if (x.Color.Type === value)
x.Selected = false;
});
} else {
let contains = this.colorValue.indexOf(value) > -1;
if (contains) {
let selectCount = this.Numbers.filter(x => x.Color.Type === value && x.Selected === false).length;
if (this.getShowCount() + selectCount >= 30) {
this.$vux.toast.show({
text: 'So more count',
type: 'warn'
});
this.colorValue.pop();
return;
}
}
this.Numbers.forEach(x => {
if (x.Color.Type === value)
x.Selected = contains;
});
}

},
selectSingle(value) {
if (this.singleSwitch) {
this.Numbers.forEach(x => {
if (Number(x.IsSingle) === value)
x.Selected = false;
});
} else {
let contains = this.singleValue.indexOf(value) > -1;
if (contains) {
let selectCount = this.Numbers.filter(x => Number(x.IsSingle) === value && x.Selected === false).length;
if (this.getShowCount() + selectCount >= 30) {
this.$vux.toast.show({
text: 'So more count',
type: 'warn'
});
this.singleValue.pop();
return;
}
}
this.Numbers.forEach(x => {
if (Number(x.IsSingle) === value)
x.Selected = contains;
});
}
},
getShowCount() {
return this.Numbers.filter(x => x.Selected === true).length;
},
selectNumberItem(item) {
if (this.lock)
return;
if (this.getShowCount() >= 30) {
this.$vux.toast.show({
text: 'So more count',
type: 'warn'
});
} else {
item.Selected = !item.Selected
}
},
showNumberValue(item) {
if (item.Value.toString().length > 1)
return item.Value.toString();
else
return '0' + item.Value.toString();
},
animalSwitchChange() {
if(this.lock)
return;
this.animalValue = [-1];
},
numberSwitchChange() {
if(this.lock)
return;
this.numsValue = [-1];
},
colorSwitchChange() {
if(this.lock)
return;
this.colorValue = [-1];
},
singleSwitchChange() {
if(this.lock)
return;
this.singleValue = [-1];
},
selectAllClick() {
this.lock = !this.lock;
},
reset() {
this.animalValue = [-1];
this.numsValue = [-1];
this.colorValue = [-1];
this.singleValue = [-1];
this.animalSwitch = false;
this.numberSwitch = false;
this.colorSwitch = false;
this.singleSwitch = false;
this.selectAll = false;
this.lock = false;
this.Numbers.forEach(x => x.Selected = false);
}
},
watch: {}
}



