import Eumns, {Enums} from '../constants/Enums'

export class NumberItem {
  constructor() {
    this.Value = 0;
    this.IsSingle = false;
    this.TailValue = 0;
    this.Color = new ColorItem();
    this.Animal = new Animal();
    this.Selected = false;
  }
}

export class Animal {
  constructor(){
    this.Type = 0; //Eumns.AnimalType.Chook;
    this.Name = '';
    this.FiveType = 0; //Enums.FiveElementType.Fire;
    this.FiveName = '';
  }
}

export class ColorItem {
  constructor(){
    this.Type = 0; //Eumns.ColorType.Blue;
    this.Name = '';
  }
}



